.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	.nav {
		&__container {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		.logo {
			width: 12rem;
		}
		&__list {
			display: flex;
			gap: 4rem;
			align-items: center;
		}
		&__link {
			font-size: 1.6rem;
			font-weight: 500;
			color: $color-primary-dark;
			transition: all 0.2s ease;
			&:hover {
				color: $color-secondary;
			}
		}

		&__btn,
		&__btn:visited {
			color: white;
			display: block;
			border-radius: 1rem;
			padding: 1rem 2rem;
			background-image: linear-gradient(to right, $color-primary, $color-secondary);
			&:hover {
				color: #eee;
			}
		}
	}
}
